import React, {useState, useEffect} from 'react';
import {NavLink} from 'react-router-dom';
import {CaseInfo, Stars} from '../../../utils/supports';
import {arrowDown, arrowUp} from '../../../common/images/materials';
import arrow from '../../../common/images/auth/arrow-right.svg';
import './index.scss';

const files = [{title: ' .pdf', size: '76 '}, {title: ' .pdf', size: '76 '}, {title: ' .pdf', size: '76 '}]
const feedback = [
  {
    stars: 3,
    name: '  ',
    fullText: ' ,        ,     ()     .        , ,  .  ,        ,     ()     .        , ,  .',
    text: ' ,        ,     ()     .        , ,  .'
  },
  {
    stars: 5,
    name: '  ',
    fullText: ' ,        ,     ()     .        , ,  .  ,        ,     ()     .        , ,  .',
    text: ' ,        ,     ()     .        , ,  .'
  }
]

const MyCases = ({onPage}) => {
  const [cases] = useState([
    {title:'Railway One', text:'   FastCom Group,       .      .  FastCom        ,  FastCom Bank.          ,       .           .', files, feedback}])
  useEffect(() => onPage('my-cases'), [])
  return (
    <div className='my-cases'>
      {cases.length
        ? cases.map(c => <Case {...c}/>)
        : <div className='my-cases__text'>There you find your Case Challenge solutions and feedbacks</div>
      }
    </div>
  );
};

export default MyCases;

const Case = ({title, text, files, feedback}) => {
  const [isShow, setIsShow] = useState(false)

  return (
    <div className='case'>
      <div className='case-top'>
        <h3 className='case-top__title'>{title}</h3>
        <div className='case-top__arrow'>
          <img className={isShow ? 'arrow-up' : 'arrow-down'} src={isShow ? arrowUp : arrowDown} onClick={() => setIsShow(!isShow)} alt='arrow'/>
        </div>
      </div>
      {isShow &&
      <>
        <CaseInfo title=' ' text={text} files={files}/>

        <div className='case-feedback'>
          <div className='case-feedback__title'></div>
          {feedback.map(({stars, name, text, fullText}) => <Feedback key={name+stars} stars={stars} name={name} text={text} fullText={fullText}/>)}
        </div>
      </>
      }
    </div>
  )
}

const Feedback = ({stars, name, text, fullText}) => {
  const [showFullText, setShowFullText] = useState(false)
  return(
    <div className='feedback'>
    <div className='feedback__stars'>
      <Stars stars={stars}/>
    </div>
    <div className='feedback__name'>
      <NavLink to='/profile'>
        {name}
        <img src={arrow} alt='arrow'/>
      </NavLink>
    </div>
    <p className='feedback__text'>
      {showFullText ? fullText : text}
      <span className='feedback__text-read-full' onClick={() => setShowFullText(!showFullText)}>{showFullText ? '(Hide full review)' : '(Read full review)'}</span>
    </p>
  </div>
  )
}
