import React, {useState} from 'react';
import {Input, Button} from 'antd';
import cn from 'classnames';
import {close} from '../../common/images/account';
import './index.scss';

const modal = [
  {
    reason: 'delete',
    title: 'Are you sure you want to delete the contact?',
    text: '        .',
    btn1: 'Delete',
    btn2: 'No, Ive change my mind'
  },
  {reason: 'cancel', title: 'Are you sure you want to withdraw this request?', btn1: 'Yes', btn2: 'No Ive changed my mind'},
  {
    reason: 'no add',
    title: 'Are you sure you dont want to add a buddy?',
    text: '      ',
    btn1: 'Yes',
    btn2: 'No, Ive changed my mind'
  }
]

const ModalCard = ({reason, partners, partnerId, setPartners, setModal}) => {
  const onClick = (reason, btn) => {
    (reason === 'delete' || reason === 'cancel' || reason === 'no add') && btn === 1 && setPartners(partners.filter(p => p.id !== partnerId))
    reason === 'no add' && btn === 2 && setPartners(partners.map(p => p.id === partnerId ? {...p, status: 'my contact'} : p))
    setModal(null)
  }
  return (
    <div className={cn('modal-card', {'dn': !reason})}>
      <div className='modal-card__modal'>
        <div className='modal-card__close' onClick={() => setModal(null)}>
          <img src={close} alt='close'/>
        </div>
        {reason === 'report'
          ? <Report setModal={setModal}/>
          : modal.map(m => m.reason === reason && <ContactChange {...m} onClick={onClick}/>)}
      </div>
    </div>
  )
}

export default ModalCard;

const ContactChange = ({title, text, reason, btn1, btn2, onClick}) => {
  return (
    <div className='contact-change'>
      <div className='contact-change__title'>
        {title}
      </div>
      <div className='contact-change__text'>
        {text}
      </div>
      <Button className='blue' onClick={() => onClick(reason, 1)}>
        {btn1}
      </Button>
      <Button onClick={() => onClick(reason, 2)}>
        {btn2}
      </Button>
    </div>
  )
}

const Report = ({setModal}) => {
  const [text, setText] = useState('')
  const [isSent, setIsSent] = useState(false)
  return (
    <div className='report'>
      {!isSent ?
        <>
          <div className='report__title'>Report</div>
          <p className='report__text'>  . ,     .</p>
          <Input.TextArea placeholder=' ' onChange={setText}/>
          <Button className='blue' onClick={() => setIsSent(true)}></Button>
        </>
        : <>
          <div className='report__title'> </div>
          <p className='report__text'>    Case Academy.       .   .</p>
          <Button onClick={() => setModal(null)}></Button>
        </>
      }
    </div>
  )
}


export const Modal = ({showModal, setShowModal, children}) => {

  return (
    <div className={cn('modal-card', {'dn': !showModal})}>
      <div className='modal-card__modal'>
        <div className='modal-card__close' onClick={() => setShowModal(false)}>
          <img src={close} alt='close'/>
        </div>
        <div className='modal-card__child'>
          {children}
        </div>
      </div>
    </div>
  )
}
